/* ProductList.css */

.product-list {
  padding: 10px;
  padding-bottom: 10px; /* 푸터 높이만큼 패딩 추가 */
  padding-top: 10px; /* 상단 여백 추가 */
  box-sizing: border-box; /* 패딩이 콘텐츠 영역에 포함되도록 설정 */
  overflow-y: auto; /* 전체 페이지에서 세로 스크롤 추가 */
  height: calc(100vh - 80px); /* 전체 화면 높이에서 상단 패딩 및 푸터 높이 제외 */
}

.product-list-container {
  display: block; /* 블록 요소로 설정하여 한 줄에 하나씩 배치 */
  margin: 0 auto; /* 중앙 정렬 */
  max-width: 100%; /* 컨테이너의 최대 너비를 전체 폭으로 설정 */
}

.product-item {
  border: 1px solid #ddd;
  padding: 20px;
  margin: 0 0 20px 0; /* 하단 여백 추가 */
  width: calc(100% - 40px); /* 전체 폭에서 좌우 패딩을 제외 */
  box-sizing: border-box;
}

/* 제목 스타일 */
.product-list h1 {
  margin: 0 0 20px 0; /* 상단과 하단 여백 조정 */
  font-size: 2rem; /* 적절한 폰트 크기 조정 */
}
.contact-button-container {
  display: flex;
  justify-content: center;
  margin-top: 20px;
}

.contact-button {
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  background-color: #007BFF;
  color: white;
  border: none;
  border-radius: 5px;
}

.contact-button:hover {
  background-color: #0056b3;
}
/* Mypage.css */

.mypage-container {
  padding: 20px;
  max-width: 1200px;
  margin: 0 auto;
}

.search-and-register {
  display: flex;
  align-items: center;
  margin-bottom: 20px;
}

.search-input {
  flex: 1;
  padding: 8px;
  margin-right: 10px;
}

.register-button, .toggle-button {
  padding: 8px 16px;
  margin-left: 10px;
  cursor: pointer;
}

.product-list-container {
  display: flex;
  flex-wrap: wrap;
}

.product-item {
  border: 1px solid #ddd;
  padding: 10px;
  margin: 10px;
  width: calc(33.333% - 20px);
  cursor: pointer;
  border-radius: 4px;
  box-shadow: 0 0 10px rgba(0,0,0,0.1);
}

.product-item h2 {
  font-size: 18px;
  margin: 0 0 10px;
}

.product-item p {
  margin: 5px 0;
}

.product-item:hover {
  background-color: #f9f9f9;
}

.button-container {
  display: flex;
  justify-content: space-around;
  margin-bottom: 20px;
}

.button-container button {
  padding: 10px 20px;
  border: none;
  border-radius: 4px;
  background-color: #007bff;
  color: white;
  cursor: pointer;
}

.button-container button.active {
  background-color: #0056b3;
}

.button-container button.inactive {
  background-color: #ddd;
  cursor: default;
}
