/* 검색 입력 필드 스타일 */
.search-input {
  width: 200px; /* 너비 조절 (100px에서 200px로 확대) */
  padding: 8px; /* 패딩 추가 */
  border: 1px solid #ccc; /* 테두리 색상 */
  border-radius: 4px; /* 모서리 둥글게 */
  box-sizing: border-box; /* 패딩과 테두리를 포함 */
  margin-right: 10px; /* 버튼과의 간격 조정 */
  font-size: 1rem; /* 폰트 크기 조정 */
  max-width: 50%; /* 최대 너비를 부모 요소에 맞추기 */
}

/* 검색 버튼 스타일 */
.search-button {
  padding: 8px 16px; /* 패딩 추가 */
  border: none; /* 테두리 제거 */
  background-color: #007bff; /* 버튼 배경 색상 */
  color: white; /* 버튼 텍스트 색상 */
  border-radius: 4px; /* 모서리 둥글게 */
  cursor: pointer; /* 클릭 시 커서 모양 변경 */
  font-size: 1rem; /* 폰트 크기 조정 */
}

/* 검색 버튼 호버 스타일 */
.search-button:hover {
  background-color: #0056b3; /* 버튼 호버 시 배경 색상 */
}

/* 검색 결과 리스트 스타일 */
.search-results {
  list-style-type: none; /* 기본 리스트 스타일 제거 */
  padding: 0; /* 기본 패딩 제거 */
  margin-top: 10px; /* 입력 필드와 버튼 아래 여백 추가 */
  max-width: 100%; /* 최대 너비를 부모 요소에 맞추기 */
}

/* 검색 결과 항목 스타일 */
.search-results li {
  padding: 8px; /* 각 항목 패딩 추가 */
  border-bottom: 1px solid #eee; /* 항목 사이의 경계선 추가 */
}

/* 검색 결과 항목 호버 스타일 */
.search-results li:hover {
  background-color: #f5f5f5; /* 항목 호버 시 배경 색상 */
}

.start-chat-button {
    margin-left: 10px;
    padding: 5px 10px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 5px;
    cursor: pointer;
}

.start-chat-button:hover {
    background-color: #0056b3;
}
